import { Component, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import {
  AppNameService, CurrencyService, EventbusService,
  FiltersCardComponent, MainBarTitleService,
  PageContentComponent, PageHeaderComponent,
  TooltipComponent,
  WebStorageService
} from '@front-shared-lib';
import { TooltipService } from '../../../services/tooltip.service';
import { SelectTemplateComponent } from '../templates/select-template/select-template.component';
import { Receipt1Component } from '../templates/template-1/receipt-1/receipt-1.component';
import { Receipt10Component } from '../templates/template-10/receipt-10/receipt-10.component';
import { Receipt2Component } from '../templates/template-2/receipt-2/receipt-2.component';
import { Receipt3Component } from '../templates/template-3/receipt-3/receipt-3.component';
import { Receipt4Component } from '../templates/template-4/receipt-4/receipt-4.component';
import { Receipt5Component } from '../templates/template-5/receipt-5/receipt-5.component';
import { Receipt6Component } from '../templates/template-6/receipt-6/receipt-6.component';
import { Receipt7Component } from '../templates/template-7/receipt-7/receipt-7.component';
import { Receipt8Component } from '../templates/template-8/receipt-8/receipt-8.component';
import { Receipt9Component } from '../templates/template-9/receipt-9/receipt-9.component';
import { ReceiptDefaultComponent } from '../templates/template-default/receipt-default/receipt-default.component';
import { ViewReceiptBase } from './view-receipt.base';

@Component({
  selector: 'lib-view-receipt',
  standalone: true,
  imports: [
    PageContentComponent,
    PageHeaderComponent,
    SelectTemplateComponent,
    Receipt1Component,
    Receipt2Component,
    Receipt3Component,
    Receipt4Component,
    Receipt5Component,
    Receipt6Component,
    Receipt7Component,
    Receipt8Component,
    Receipt9Component,
    Receipt10Component,
    ReceiptDefaultComponent,
    FiltersCardComponent,
    TooltipComponent
  ],
  templateUrl: './view-receipt.component.html',
  styleUrls: ['./view-receipt.component.scss']
})
export class ViewReceiptComponent
  extends ViewReceiptBase
  implements OnInit, OnDestroy {
  template = 'default';
  templatesOpen = false;

  constructor(
    protected override route: ActivatedRoute,
    protected override router: Router,
    protected override eventbus: EventbusService,
    public override currencyService: CurrencyService,
    public override tooltipService: TooltipService,
    protected override apNme: AppNameService,
    protected override mainBarTitle: MainBarTitleService,
    private storage: WebStorageService,
    protected override metaTagService: Meta,
    protected override titleService: Title
  ) {
    super(
      route,
      router,
      eventbus,
      currencyService,
      tooltipService,
      apNme,
      mainBarTitle,
      metaTagService,
      titleService
    );
  }

  override ngOnInit(): void {
    super.ngOnInit();
    this.template = this.storage.get(this.apNme + 'currentTemplate') || 'default';
  }

  templateSelected(template: string) {
    this.template = template;
    this.templatesOpen = false;
    this.storage.set(this.apNme + 'currentTemplate', template);
  }

  toggletemplatesOpen() {
    this.templatesOpen = !this.templatesOpen;
  }

  print() {
    window.print();
  }

  override ngOnDestroy() {
    super.ngOnDestroy();
  }
}

