import { DatePipe, NgClass } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Router } from '@angular/router';
import { CompanySettings } from '@biz-nest/commerce-client';
import { FrontendLogger, getBizNestErrorResponseMessage, IcompanySetting } from '@biz-nest/core-client';
import { CurrencyService, DataService, EventbusService, TooltipService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Editor, NgxEditorModule, Toolbar } from 'ngx-editor';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'lib-template-company-info',
  standalone: true,
  imports: [
    DatePipe,
    FormsModule,
    NgxEditorModule,
    NgClass
  ],
  templateUrl: './template-company-info.component.html',
  styleUrls: ['../../styles/template.scss', './template-company-info.component.scss']
})
export class TemplateCompanyInfoComponent
implements OnInit, OnDestroy {
  html = `
  <b>Invar Photo studio:</b>
                <address>
                  4510 E 96th St, Indianapolis,<br />125 CXR United States<br />{{
                    "info@invcephotostudio.com"
                  }}<br />+153 3256 3548
                </address>
  `;

  editText = 'Edit';
  posting = false;
  companySetting?: CompanySettings;
  cleanup?: () => void;

  // editor
  editor?: Editor;
  toolbar: Toolbar = [
    ['bold', 'italic'],
    ['underline', 'strike'],
    ['code', 'blockquote'],
    ['ordered_list', 'bullet_list'],
    [{ heading: ['h1', 'h2', 'h3', 'h4', 'h5', 'h6'] }],
    ['link', 'image'],
    ['text_color', 'background_color'],
    ['align_left', 'align_center', 'align_right', 'align_justify']
  ];

  constructor(
    protected modalService: BsModalService,
    protected eventbus: EventbusService,
    public dataService: DataService,
    public currencyService: CurrencyService,
    public tooltipService: TooltipService,
    protected toastr: ToastrService,
    protected router: Router
  ) {

  }

  ngOnInit(): void {
    this.editor = new Editor();
    this.getAll();
    const handlers = {
      companySettingsFromFoot: (data) => {
        this.setCompanySettings(data);
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }

  async getAll() {
    const { dataArr } = await CompanySettings
      .getAll();

    this.companySetting = dataArr[0];
    this.eventbus.generalEvent$.next({ type: 'companySettingsFromHead', data: this.companySetting });
    this.setCompanySettings(dataArr[0]);
    this.html = this.companySetting?.printDetails?.html || '';
    // this.commonService.changeCurrency(this.companySetting?.documentSettings?.currency);
  }

  setCompanySettings(companySetting: CompanySettings) {
    this.companySetting = companySetting;
    this.html = this.companySetting?.printDetails?.html ||
    typeof this.dataService.currentCompany?.owner === 'object' &&
    this.dataService.currentCompany?.displayName ||
    'header holder';
  }

  closeEditor() {
    this.editText = 'Edit';
  }


  async editHead() {
    if (this.editText === 'Edit') {
      this.editText = 'Save';
    } else {
      try {
        this.posting = true;
        const vals = {
          printDetails: {
            html: this.html || ''
          }
        } as IcompanySetting;

        await CompanySettings.addUpdate(vals);

        this.toastr.success('Operation sucessfull...!', 'Success');
        this.getAll();

        this.editText = 'Edit';
      } catch (error) {
        FrontendLogger.error('InvoicesSettingsComponent:saveChanges:: - err ', error);

        this.toastr.error(getBizNestErrorResponseMessage(error), 'Failed', {
          disableTimeOut: true
        });
      } finally {
        this.posting = false;
      }
    }
  }

  editedFoot(ev) {
    this.eventbus.generalEvent$.next({
      type: 'editedFoot',
      data: ev
    });
  }

  printDocument() {
    this.editText = 'Edit';
    this.eventbus.generalEvent$.next({ type: 'printDocument', data: true });
    setTimeout(() => {
      window.print();
    }, 300);

    // this.onPrint();
    // this.printData();
  }

  printData() {
    const divToPrint = document.getElementById('printTable');

    if (!divToPrint) {
      return;
    }

    const newWin = window.open('');

    newWin?.document.write(divToPrint.outerHTML);
    newWin?.print();
    newWin?.close();
  }


  printReceipt(el) {
    const w = window.open();

    const elem = document.getElementById(el)?.innerHTML;

    if (!elem) {
      return;
    }

    w?.document.write('<html><head><title></title>');
    w?.document.write('<link rel="stylesheet" type="text/css" href="css/receipt-print.css">');
    w?.document.write('</head><body >');
    w?.document.write(elem);
    w?.document
      .write(`<script type="text/javascript">
      addEventListener("load", () => { print(); close(); })
      </script></body></html>`);

    w?.document.close();
    w?.focus();
  }

  onPrint(divName = 'tryid') {
    const printContents = document.getElementById(divName)?.innerHTML;

    if (!printContents) {
      return;
    }
    const originalContents = document.body.innerHTML;

    document.body.innerHTML = printContents;
    window.print();
    document.body.innerHTML = originalContents;
  };

  ngOnDestroy() {
    this.editor?.destroy();
  }
}
