import { Directive, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { CompanySettings } from '@biz-nest/commerce-client';
import { CurrencyService, DataService, EventbusService, TooltipService } from '@front-shared-lib';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';

@Directive({})
export class TemplateHeadBaseComponent
implements OnInit {
  @Input() header;
  @Input() id?: string;
  @Input() idLabel?: string;
  @Input() balanceDue?: number;
  @Input() date?: Date;
  @Input() ammountRcievd?: number;
  @Output() notify: EventEmitter<unknown> = new EventEmitter<unknown>();
  logo = '';
  companySetting?: CompanySettings;
  cleanup?: () => void;


  constructor(
    protected modalService: BsModalService,
    protected eventbus: EventbusService,
    public dataService: DataService,
    public currencyService: CurrencyService,
    public tooltipService: TooltipService,
    protected toastr: ToastrService,
    protected router: Router
  ) { }

  ngOnInit(): void {
    if (typeof this.dataService.currentCompany &&
        this.dataService.currentCompany?.logo) {
      this.logo = this.dataService.currentCompany?.logo.url;
    }
  }


  logoClick() {
    if (!this.logo) {
      this.router.navigate(['/setting/company-setting']);
    }
  }
}
