import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { Component, HostListener, Input, OnDestroy, OnInit } from '@angular/core';
import { CompanySettings } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import { WebStorageService } from '@front-shared-lib';

@Component({
  selector: 'lib-stamp',
  standalone: true,
  imports: [
    CommonModule,
    DragDropModule
  ],
  templateUrl: './stamp.component.html',
  styleUrls: ['./stamp.component.scss']
})
export class StampComponent
implements OnInit, OnDestroy {
  @Input({ required: true }) storeStr!: string;
  size = { width: 200, height: 100 };
  position = { x: 100, y: 100 };
  stampPhoto = '';
  private isResizing = false;
  private startX = 0;
  private startY = 0;
  private startWidth = 0;
  private startHeight = 0;

  constructor(private storage: WebStorageService) {}

  startResize(event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();
    this.isResizing = true;
    this.startX = event.clientX;
    this.startY = event.clientY;
    this.startWidth = this.size.width;
    this.startHeight = this.size.height;
  }

  @HostListener('document:mousemove', ['$event'])
  onMouseMove(event: MouseEvent): void {
    if (!this.isResizing) {
      return;
    }

    const width = Math.max(100, this.startWidth + (event.clientX - this.startX));
    const height = Math.max(60, this.startHeight + (event.clientY - this.startY));

    this.size = { width, height };
    this.storage.set(this.storeStr + 'stamp-size-width', this.size.width.toString());
    this.storage.set(this.storeStr + 'stamp-size-height', this.size.height.toString());
  }

  @HostListener('document:mouseup')
  onMouseUp(): void {
    this.isResizing = false;
    this.storage.set(this.storeStr + 'stamp-size-width', this.size.width.toString());
    this.storage.set(this.storeStr + 'stamp-size-height', this.size.height.toString());
  }


  ngOnInit(): void {
    this.size.width = Number(this.storage.get(this.storeStr + 'stamp-size-width')) || this.size.width;
    this.size.height = Number(this.storage.get(this.storeStr + 'stamp-size-height')) || this.size.height;
    this.position.x = Number(this.storage.get(this.storeStr + 'stamp-position-x')) || this.position.x;
    this.position.y = Number(this.storage.get(this.storeStr + 'stamp-position-y')) || this.position.y;
    this.getSettings();
  }

  async getSettings() {
    try {
      const { dataArr } = await CompanySettings.getAll();

      if (dataArr.length > 0) {
        const companySetting = dataArr[0];

        if (typeof companySetting.documentSettings?.digitalStamp === 'object') {
          this.stampPhoto = companySetting.documentSettings?.digitalStamp?.url;
        }
      }
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  onDragEnded(event): void {
    const { x, y } = event.source.getFreeDragPosition();


    // this.position = { x, y };
    this.storage.set(this.storeStr + 'stamp-position-x', x.toString());
    this.storage.set(this.storeStr + 'stamp-position-y', y.toString());
  }

  onDragMove(event): void {
    const { x, y } = event.source.getFreeDragPosition();

    this.storage.set(this.storeStr + 'stamp-position-x', x.toString());
    this.storage.set(this.storeStr + 'stamp-position-y', y.toString());
  }

  ngOnDestroy(): void {
    this.storage.set(this.storeStr + 'stamp-size-width', this.size.width.toString());
    this.storage.set(this.storeStr + 'stamp-size-height', this.size.height.toString());
  }
}
