import { DatePipe, NgClass } from '@angular/common';
import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CompanySettings } from '@biz-nest/commerce-client';
import { FrontendLogger, getBizNestErrorResponseMessage, IcompanySetting, IfileMeta } from '@biz-nest/core-client';
import { CurrencyService, EventbusService, TooltipService } from '@front-shared-lib';
import { runSubscriptionsForSharedUse } from '@main-shared-lib';
import { Editor, NgxEditorModule, Toolbar } from 'ngx-editor';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'lib-template-footer',
  standalone: true,
  imports: [
    DatePipe,
    FormsModule,
    NgxEditorModule,
    NgClass
  ],
  encapsulation: ViewEncapsulation.None,
  templateUrl: './template-footer.component.html',
  styleUrls: ['./template-footer.component.scss']
})
export class TemplateFooterComponent
implements OnInit, OnDestroy {
  cleanup?: () => void;
  nowDate = new Date();
  stampPhoto = ''; // 'assets/auth-imgs/eagleinfo-stamp.png';
  signaturePhoto = ''; // 'assets/auth-imgs/george-signature.png';
  bottomHaeding1Bool = 'Edit';
  bottomDesc1Bool = 'Edit';
  bottomHaeding2Bool = 'Edit';
  bottomDesc2Bool = 'Edit';
  posting = false;
  companySetting?: CompanySettings;

  // editor
  editor?: Editor;
  html = `
  <div class="col-auto">
              <div class="invoice-right">
                <b>Pay To:</b>
                <address>Payment Info:<br />Account : 1234 5678 9012<br />A/C Name :
                  Alex Farnandes<br />Emil : {{ "info@invar.com" }}
                </address>
              </div>
            </div>
  `;

  toolbar: Toolbar = [
    ['bold', 'italic'],
    ['underline', 'strike'],
    ['code', 'blockquote'],
    ['ordered_list', 'bullet_list'],
    [{ heading: ['h1', 'h2', 'h3', 'h4', 'h5', 'h6'] }],
    ['link', 'image'],
    ['text_color', 'background_color'],
    ['align_left', 'align_center', 'align_right', 'align_justify']
  ];

  constructor(
    public tooltipService: TooltipService,
    public currencyService: CurrencyService,
    protected toastr: ToastrService,
    protected eventbus: EventbusService
  ) { }

  ngOnInit() {
    this.editor = new Editor();
    this.getAll();
    const handlers = {
      printDocument: (data) => {
        this.bottomHaeding1Bool = 'Edit';
        this.bottomDesc1Bool = 'Edit';
        this.bottomHaeding2Bool = 'Edit';
        this.bottomDesc2Bool = 'Edit';
      },
      companySettingsFromHead: (data) => {
        this.setCompanySettings(data);
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
  }

  async getAll() {
    const { dataArr } = await CompanySettings
      .getAll();

    this.companySetting = dataArr[0];
    this.setCompanySettings(dataArr[0]);
    /// this.commonService.changeCurrency(this.companySetting?.documentSettings?.currency);
    if ((this.companySetting.documentSettings?.digitalSignature as IfileMeta)?.url) {
      this.signaturePhoto = (this.companySetting.documentSettings?.digitalSignature as IfileMeta).url;
    }
    if ((this.companySetting.documentSettings?.digitalStamp as IfileMeta)?.url) {
      this.stampPhoto = (this.companySetting.documentSettings.digitalStamp as IfileMeta).url;
    }
    this.eventbus.generalEvent$.next({ type: 'companySettingsFromFoot', data: this.companySetting });
  }

  setCompanySettings(companySetting: CompanySettings) {
    this.companySetting = companySetting;
  }

  closeEditor() {
    this.bottomDesc1Bool = 'Edit';
  }

  async editFoot(field: string) {
    if (this[field] === 'Edit') {
      this[field] = 'Save';
    } else {
      try {
        this.posting = true;
        const vals = {
          printDetails: {
            html: this.companySetting?.printDetails?.html
          }
        } as IcompanySetting;

        await CompanySettings.addUpdate(vals);

        this.toastr.success('Operation sucessfull...!', 'Success');

        this.getAll();

        this[field] = 'Edit';
      } catch (error) {
        FrontendLogger.error('TemplateFooterComponent:editFoot:: - err ', error);
        this.toastr.error(getBizNestErrorResponseMessage(error), 'Failed', {
          disableTimeOut: true
        });
      } finally {
        this.posting = false;
      }
    }
  }

  ngOnDestroy() {
    this.editor?.destroy();
    this.cleanup?.();
  }
}
