import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule, DatePipe } from '@angular/common';
import { Component, HostListener, Input, OnDestroy, OnInit } from '@angular/core';
import { CompanySettings } from '@biz-nest/commerce-client';
import { WebStorageService } from '@front-shared-lib';

@Component({
  selector: 'lib-stamp-date',
  standalone: true,
  imports: [
    CommonModule,
    DragDropModule,
    DatePipe
  ],
  templateUrl: './stamp-date.component.html',
  styleUrls: ['./stamp-date.component.scss']
})
export class StampDateComponent
implements OnInit, OnDestroy {
  @Input({ required: true }) storeStr!: string;
  size = { width: 200, height: 100 };
  position = { x: 100, y: 100 };
  stampPhoto = ''; // 'assets/auth-imgs/eagleinfo-stamp.png';
  signaturePhoto = ''; // 'assets/auth-imgs/george-signature.png';
  companySetting?: CompanySettings;
  private isResizing = false;
  private startX = 0;
  private startY = 0;
  private startWidth = 0;
  private startHeight = 0;
  nowDate = new Date();

  constructor(private storage: WebStorageService) {}

  startResize(event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();
    this.isResizing = true;
    this.startX = event.clientX;
    this.startY = event.clientY;
    this.startWidth = this.size.width;
    this.startHeight = this.size.height;
  }

  @HostListener('document:mousemove', ['$event'])
  onMouseMove(event: MouseEvent): void {
    if (!this.isResizing) {
      return;
    }

    const width = Math.max(100, this.startWidth + (event.clientX - this.startX));
    const height = Math.max(60, this.startHeight + (event.clientY - this.startY));

    this.size = { width, height };
    this.storage.set(this.storeStr + 'stamp-date-size-width', this.size.width.toString());
    this.storage.set(this.storeStr + 'stamp-date-size-height', this.size.height.toString());
  }

  @HostListener('document:mouseup')
  onMouseUp(): void {
    this.isResizing = false;
    this.storage.set(this.storeStr + 'stamp-date-size-width', this.size.width.toString());
    this.storage.set(this.storeStr + 'stamp-date-size-height', this.size.height.toString());
  }


  ngOnInit(): void {
    this.size.width = Number(this.storage.get(this.storeStr + 'stamp-date-size-width')) || this.size.width;
    this.size.height = Number(this.storage.get(this.storeStr + 'stamp-date-size-height')) || this.size.height;
    this.position.x = Number(this.storage.get(this.storeStr + 'stamp-date-position-x')) || this.position.x;
    this.position.y = Number(this.storage.get(this.storeStr + 'stamp-date-position-y')) || this.position.y;
  }

  onDragEnded(event): void {
    const { x, y } = event.source.getFreeDragPosition();

    this.storage.set(this.storeStr + 'stamp-date-position-x', x.toString());
    this.storage.set(this.storeStr + 'stamp-date-position-y', y.toString());
  }

  onDragMove(event): void {
    const { x, y } = event.source.getFreeDragPosition();

    this.storage.set(this.storeStr + 'stamp-date-position-x', x.toString());
    this.storage.set(this.storeStr + 'stamp-date-position-y', y.toString());
  }

  ngOnDestroy(): void {
    this.storage.set(this.storeStr + 'stamp-date-size-width', this.size.width.toString());
    this.storage.set(this.storeStr + 'stamp-date-size-height', this.size.height.toString());
  }
}
