import { Directive, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { Receipt } from '@biz-nest/commerce-client';
import { FrontendLogger } from '@biz-nest/core-client';
import {
  AppNameService, CurrencyService, EventbusService, MainBarTitleService
} from '@front-shared-lib';
import { navByBus, runSubscriptionsForSharedUse } from '@main-shared-lib';
import { TooltipService } from '../../../services/tooltip.service';

@Directive({
  standalone: true
})
export class ViewReceiptBase
implements OnInit, OnDestroy {
  receipt?: Receipt;
  id: string;
  cleanup?: () => void;

  loading = false;
  getErr?: string;

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  constructor(
    protected route: ActivatedRoute,
    protected router: Router,
    protected eventbus: EventbusService,
    public currencyService: CurrencyService,
    public tooltipService: TooltipService,
    protected apNme: AppNameService,
    protected mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) {
    this.id = this.route.snapshot?.params['id'];
  }

  ngOnInit(): void {
    const handlers = {
      oneItem: (data) => {
        this.receipt = data;
        this.mainBarTitle.title = 'View Receipt - ' + data.urId;
      }
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.setTitle();
    this.cleanup = cleanup;
    if (!this.receipt) {
      this.getReceipt();
    }
  }

  setTitle() {
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'View receipt details including balance due and amount received'
    });
  }

  async getReceipt() {
    try {
      this.loading = true;
      this.getErr = '';
      const receipt = await Receipt.getOne(this.id);

      if (receipt) {
        this.receipt = receipt;
        this.setTitle();
      }
      this.loading = false;
    } catch (error) {
      FrontendLogger.error(error);
      this.getErr = error.code;
    } finally {
      this.loading = false;
    }
  }

  updateReceipt(): void {
    this.navigateByBus(['/receipt/update'], 'updateReceipt', this.receipt);
  }

  outEve(ev) {
    switch (ev.route) {
      case 'main':
        this.updateReceipt();
        break;
    }
  }

  // TODO make pdf
  gneratePdf() {

  }

  ngOnDestroy() {
    this.cleanup?.();
  }
}

